import Page from '../components/Page';
import React from 'react';
import { Card, CardBody, CardHeader, Col, Row, Table, Button, Alert, Modal,
    ModalBody,
    ModalFooter,
    ModalHeader,ButtonGroup,
    Input,
    UncontrolledAlert
} from 'reactstrap';
import connect from "react-redux/es/connect/connect";
import {listJobs} from '../store/actions/jobs';
import ReactPaginate from 'react-paginate';
import { Link } from "react-router-dom";
import * as queryString from "query-string";
import { history } from "App.js";
import { getDeleteJobs, getSearchJobs, getActiveJobs} from '../store/actions/jobs';

class JobsPage extends React.Component {

    constructor(props) {
        super(props);
        this.props.listJobs(1,10);
        this.timeout =  0;
    }
    state = {
        modal: false,
        id: null,
        title: null,
        keywords:"",
        status:""
    };

    toggle = () => {
        return this.setState({
            modal: !this.state.modal,
        });

    };
    delete = (id)=>{
        this.props.getDeleteJobs(id);
        this.toggle();
        history.push('/jobs');

    };
    handlePageClick(data){
        if(this.state.keywords||this.state.status){
            this.props.getSearchJobs(this.state.keywords,this.state.status,parseInt(data.selected)+1,10)
        }else{
            this.props.listJobs(parseInt(data.selected)+1,10);
        }
    }
    search = (e) =>{
            this.setState({keywords:e.target.value});
            if(this.timeout) clearTimeout(this.timeout);
            this.timeout = setTimeout(() => {
                this.props.getSearchJobs(this.state.keywords,this.state.status)
            }, 1000);
    };
    status = async (e) =>{
        await this.setState({status:e.target.value});
        await this.props.getSearchJobs(this.state.keywords,this.state.status);


    }
    render() {
        let errMsg;
        if(this.props.error && this.props.error.errorMsg){
            errMsg = <UncontrolledAlert color="danger">
                {this.props.error.errorMsg}
            </UncontrolledAlert>
        }
        let alert = null;
        if(this.props.location.search){
            let parsed = queryString.parse(this.props.location.search);

            if(parsed.success){
                alert = <Alert color="success">
                    To thnh cng
                </Alert>;
            }
        }
        let data;
        if (this.props.list) {
            data = this.props.list.map((job, i) => {
                let active_button;
                if(job.status === "draft"){
                    active_button = <Button color="primary" onClick={() => {
                        this.props.getActiveJobs(job._id)
                    }}>Active</Button>
                }
                return (<tr key={i}>
                    <th scope="row">{(parseInt(i)+1) + (parseInt(this.props.page)-1)*10 }</th>
                    <td>{job.title}</td>
                    <td>{job.country}</td>
                    <td>{job.cv_location}</td>
                    <td>
                        <ButtonGroup className="mr-3 mb-3">
                            <Button color="info"><div className="button-detail"><Link to={"/jobs/"+job._id}>detail</Link></div></Button>
                            <Button color="danger" onClick={()=>{ this.setState({id:job._id, title:job.title});this.toggle()}}>Delete</Button>
                            {active_button}
                        </ButtonGroup>

                    </td>
                </tr>)
            })
        }
        return (
            <Page
                title="Jobs"
                breadcrumbs={[{ name: 'jobs', active: true }]}
                className="TablePage"
            >
                <Link to={"/jobs/create"}><Button color='success'>To n hng</Button></Link>
                {alert}
                {errMsg}
                <Row>
                    <Col>
                        <Card className="mb-3">
                            <Modal
                                isOpen={this.state.modal}
                                toggle={this.toggle}
                                className={this.props.className}>
                                <ModalHeader toggle={this.toggle}>Modal title</ModalHeader>
                                <ModalBody>
                                    Bn c chc mun xa {this.state.title}
                                </ModalBody>
                                <ModalFooter>
                                    <Button color="primary" onClick={()=>this.delete(this.state.id)}>
                                        Delete
                                    </Button>{' '}
                                    <Button color="secondary" onClick={this.toggle}>
                                        Cancel
                                    </Button>
                                </ModalFooter>
                            </Modal>
                            <CardHeader>Job list</CardHeader>
                            <CardBody>
                                    <Row>
                                        <Col xl={7} lg={7} md={7}>
                                        <Input className="mb-2" onChange={this.search} type="Search" placeholder="search" bsSize="sm" />
                                        </Col>
                                        <Col xl={3} lg={3} md={3}>
                                            <Input type="select" name="status" onChange={this.status}
                                            >
                                                <option value="">Tt c</option>
                                                <option value='draft'>Draft</option>
                                                <option value='active'>Active</option>
                                                <option value='inactive'>Inactive</option>
                                                <option value='publish'>Public</option>
                                            </Input>
                                        </Col>
                                    </Row>
                                    <Row>
                                        <Col>
                                            <Card body>
                                                <Table>
                                                    <thead>
                                                    <tr>
                                                        <th>#</th>
                                                        <th>Title</th>
                                                        <th>Country</th>
                                                        <th>Cv Location</th>
                                                        <th>detail</th>
                                                    </tr>
                                                    </thead>
                                                    <tbody>
                                                    {data}
                                                    </tbody>
                                                </Table>
                                            </Card>
                                        </Col>
                                    </Row>
                            </CardBody>
                        </Card>
                    </Col>
                </Row>
                <ReactPaginate
                    previousLabel={'previous'}
                    nextLabel={'next'}
                    breakLabel={'...'}
                    breakClassName={'break-me'}
                    pageCount={this.props.totalPages}
                    marginPagesDisplayed={2}
                    pageRangeDisplayed={5}
                    onPageChange={this.handlePageClick.bind(this)}
                    containerClassName={'pagination'}
                    subContainerClassName={'pages pagination'}
                    activeClassName={'active'}
                />
            </Page>
        );
    }
}

export default connect((state) => {
    return {
        totalDocs: state.jobs.list.totalDocs,
        limit: state.jobs.list.limit,
        hasPrevPage: state.jobs.list.hasPrevPage,
        hasNextPage: state.jobs.list.hasNextPage,
        page: state.jobs.list.page,
        totalPages: state.jobs.list.totalPages,
        pagingCounter: state.jobs.list.pagingCounter,
        prevPage: state.jobs.list.prevPage,
        nextPage: state.jobs.list.nextPage,
        list: state.jobs.list.docs,
        error: state.common.requests.error,

    }
}, {listJobs,getDeleteJobs,getSearchJobs,getActiveJobs})(JobsPage);
