import React from 'react';


import {
  Row,
  Col,
} from 'reactstrap';

import Page from 'components/Page';
import 'react-dates/lib/css/_datepicker.css';
import 'react-dates/initialize';
import { DateRangePicker } from 'react-dates';
import * as moment from 'moment';


import { NumberWidget } from 'components/Widget';
import connect from "react-redux/es/connect/connect";
import {getStatisticsUser} from "../store/actions/users";
import 'moment/locale/vi';

class DashboardPage extends React.Component {
  componentDidMount() {
    // this is needed, because InfiniteCalendar forces window scroll
    window.scrollTo(0, 0);
  }
  constructor(props){
      super(props)
      this.props.getStatisticsUser({start_date:moment(this.state.startDate).format('DD-MM-YYYY'),end_date:moment(this.state.endDate).format('DD-MM-YYYY')})
  }
  state={
      startDate:moment(),
      endDate: moment(),
      focusedInput:null,
  }
  selectDateTime = (startDate,endDate)=>{
      this.setState({ startDate, endDate });
      this.props.getStatisticsUser({start_date:moment(this.state.startDate).format('DD-MM-YYYY'),end_date:moment(this.state.endDate).format('DD-MM-YYYY')})
  }
  render() {
    return (
      <Page
        className="DashboardPage"
        title="Dashboard"
        breadcrumbs={[{ name: 'Dashboard', active: true }]}>
        <Row>
          <Col lg={6} md={6} sm={6} xs={12}>
              <DateRangePicker
                  isOutsideRange={() => false}
                  startDate={this.state.startDate} // momentPropTypes.momentObj or null,
                  startDateId="your_unique_start_date_id" // PropTypes.string.isRequired,
                  endDate={this.state.endDate} // momentPropTypes.momentObj or null,
                  endDateId="your_unique_end_date_id" // PropTypes.string.isRequired,
                  onDatesChange={({ startDate, endDate }) => this.selectDateTime(startDate, endDate)} // PropTypes.func.isRequired,
                  focusedInput={this.state.focusedInput} // PropTypes.oneOf([START_DATE, END_DATE]) or null,
                  onFocusChange={focusedInput => this.setState({ focusedInput })} // PropTypes.func.isRequired,
              />
            <NumberWidget
              title="Tt c user"
              subtitle="Phn trm user"
              number={this.props.statistics.user}
              color="secondary"
              progress={{
                value: this.props.statistics.user/this.props.statistics.total*100,
                label: "T "+moment(this.state.startDate).format('DD-MM-YYYY')+" n"+moment(this.state.endDate).format('DD-MM-YYYY'),
              }}
            />
          </Col>
        </Row>
      </Page>
    );
  }
}
export default connect((state) => {
    return {
        statistics: state.users.statistics
    }
}, {getStatisticsUser})(DashboardPage);

