import Page from '../components/Page';
import React from 'react';
import { Link } from "react-router-dom";
import * as queryString from 'query-string';
import * as moment from 'moment';

import {
    Button,
    Card,
    CardBody,
    CardText,
    CardTitle,
    Col,
    Row,
    Alert,
    UncontrolledAlert
} from 'reactstrap';
import connect from "react-redux/es/connect/connect";
import { getDetailJobs, getDeleteJobs } from '../store/actions/jobs';
import { getDetailJobState, getExperienceState, getSalaryState, getJobTypeState } from '../store/selectors/job';
import { getBrokerJobState } from '../store/selectors/broker';

class JobsDetailPage extends React.Component {

    constructor(props) {
        super(props);
        if(this.props.match.params.id){
            this.props.getDetailJobs(this.props.match.params.id);
        }
    }

    render() {
        let errMsg;
        if(this.props.error && this.props.error.errorMsg){
            errMsg = <UncontrolledAlert color="danger">
                {this.props.error.errorMsg}
            </UncontrolledAlert>
        }
        let alert = null;
        if(this.props.location.search){
            let parsed = queryString.parse(this.props.location.search);

            if(parsed.success){
                alert = <Alert color="success">
                    Cp nht thnh cng
                </Alert>;
            }
        }
            return (
                <Page title="Job detail" breadcrumbs={[{ name: 'job detail', active: true }]}>
                    <Col md="12" sm="12" xs="12">
                        {alert}
                        {errMsg}
                            <Card>

                        </Card>
                    </Col>
                    <CardBody>
                        <Link to={'/jobs/edit/'+this.props.detail._id} ><Button color="primary">Edit</Button></Link>
                    </CardBody>
                    <Row>
                        <Col md={12} sm={12} xs={12} className="mb-12">
                            <Card className="flex-row">
                                <CardBody>
                                    <CardTitle>{this.props.detail.title}</CardTitle>
                                    <CardText>
                                        <b>M T</b>: {this.props.detail.description}
                                    </CardText>
                                    <CardText>
                                        <b>NC TUYN DNG:</b> {this.props.detail.country}
                                    </CardText>
                                    <CardText>
                                        <b>NI NP H S:</b> {this.props.detail.cv_location}
                                    </CardText>
                                    <CardText>
                                        <b>IU KIN TUYN DNG:</b> {this.props.detail.required_condition}
                                    </CardText>
                                    <CardText>
                                        <b>KINH NGHIM:</b> {this.props.experience}
                                    </CardText>
                                    <CardText>
                                        <b>QUYN LI:</b> {this.props.detail.benefit}
                                    </CardText>
                                    <CardText>
                                        <b>NGY HT HN:</b> {this.props.detail.deadline?moment(this.props.detail.deadline).format('YYYY-MM-DD'):null}
                                    </CardText>
                                    <CardText>
                                        <b>S LNG:</b> {this.props.detail.amount}
                                    </CardText>
                                    <CardText>
                                        <b>LNG:</b> {this.props.salary}
                                    </CardText>
                                    <CardText>
                                        <b>LOI NGNH NGH:</b> {this.props.job_type}
                                    </CardText>
                                    <CardText>
                                        <b>GII TNH:</b> {this.props.detail.gender}
                                    </CardText>
                                    <CardText>
                                        <b>SLUG:</b> {this.props.detail.slug}
                                    </CardText>
                                    <CardText>
                                        <b>NGI NG:</b> {this.props.broker}
                                    </CardText>
                                    <CardText>
                                        <b>TO NGY:</b> {this.props.detail.timeCreated?moment(this.props.detail.timeCreated).format('YYYY-MM-DD HH:mm:ss'):null}
                                    </CardText>
                                    <CardText>
                                        <b>CP NHT NGY:</b> {this.props.detail.updatedAt?moment(this.props.detail.updatedAt).format('YYYY-MM-DD HH:mm:ss'):null}
                                    </CardText>


                                </CardBody>
                            </Card>
                        </Col>
                    </Row>
                </Page>
            );
        }
}

export default connect((state) => {
    return  {
        job_type: getJobTypeState(state),
        salary: getSalaryState(state),
        experience: getExperienceState(state),
        detail: getDetailJobState(state),
        broker: getBrokerJobState(state),
        error: state.common.requests.error,
    }
}, {getDetailJobs,getDeleteJobs})(JobsDetailPage);
